//
// Font Family
//

@function get-font-family($key) {
  @return map-get($font-family, $key);
}

// IEline-height
// YuGothic,
// 'Yu Gothic',
// ,
// ,

$font-family-sans-serif:
  Linotte,
  ' Pro W3',
  'Hiragino Kaku Gothic Pro',
  Roboto,
  ,
  Meiryo,
  ' ',
  Osaka,
  'MS PGothic',
  Arial,
  Helvetica,
  Verdana,
  sans-serif;

// IEline-height
// "",
// YuMincho,
$font-family-serif:
  "Hiragino Mincho ProN",
  "Hiragino Mincho Pro",
  " ",
  serif;

$font-family: ();
$font-family: map-set($font-family, sans-serif, $font-family-sans-serif);
$font-family: map-set($font-family, serif     , $font-family-serif);
$font-family: map-set($font-family, base      , get-font-family(sans-serif));
$font-family: map-set($font-family, accent    , get-font-family(serif));
